/* ===========   CompanySize   =========== */

.circle-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}



.circle {
  position: relative;
  margin: 5px;

 display: -ms-flexbox;

 display: flex;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;
  height: 170px;
  width: 170px;

  -webkit-transition: 0.4s ease;

  -o-transition: 0.4s ease;

  transition: 0.4s ease;
}

.inner-circle {
  background-color: #44E6FF;
  height: 100px;
  width: 100px;
  -webkit-box-shadow: 3px 3px 5px rgba(0,0,0,0.2);
          box-shadow: 3px 3px 5px rgba(0,0,0,0.2);
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

}

.circle:hover .company-size-info {
  display: block;
  opacity: 1;
}

.circle:nth-child(1) .inner-circle {
  height: 50px;
  width: 50px;
    background-color: #44E6FF;
}
.circle:nth-child(2) .inner-circle {
  height: 100px;
  width: 100px;
    background-color: #44E6FF;
}
.circle:nth-child(3) .inner-circle {
  height: 130px;
  width: 130px;
  background-color: #44E6FF;
}
.circle:nth-child(4) .inner-circle {
  height: 160px;
  width: 160px;
  background-color: #44E6FF;
}


.company-size-info {
  color: #fff;
  display: none;
  opacity: 0;
  position: absolute;
  top: 110%;
  left: -50%;
  z-index: 1000;
  padding: 1em;
  background-color: rgba(0,0,0,0.5);
  width: 17.5em;
  text-align: center;
  border-radius: 5px;
}

.company-size-info>p {
  margin: 5px;
}

.company-size-info p:nth-of-type(1) {
/*text-decoration: underline;*/
  font-size: 1.2rem;
}
.company-size-info p:nth-of-type(2) {
  font-weight: bold;
  font-size: 2rem;
}






