/* ====== Animations ======== */

.animate-panel1 {
  -webkit-animation-name: moveToLeft1;
          animation-name: moveToLeft1;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}
.animate-panel2 {
  -webkit-animation-name: moveToLeft2;
          animation-name: moveToLeft2;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}
.animate-panel3 {
  -webkit-animation-name: moveToLeft3;
          animation-name: moveToLeft3;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}
.animate-panel4 {
  -webkit-animation-name: moveToLeft4;
          animation-name: moveToLeft4;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}
.animate-panel5 {
  -webkit-animation-name: moveToLeft5;
          animation-name: moveToLeft5;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
}

@-webkit-keyframes moveToLeft1 {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}

@keyframes moveToLeft1 {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}

@-webkit-keyframes moveToLeft2 {
  from {
    left: -100%;
  }
  to {
    left: -200%;
  }
}

@keyframes moveToLeft2 {
  from {
    left: -100%;
  }
  to {
    left: -200%;
  }
}
@-webkit-keyframes moveToLeft3 {
  from {
    left: -200%;
  }
  to {
    left: -300%;
  }
}
@keyframes moveToLeft3 {
  from {
    left: -200%;
  }
  to {
    left: -300%;
  }
}
@-webkit-keyframes moveToLeft4 {
  from {
    left: -300%;
  }
  to {
    left: -400%;
  }
}
@keyframes moveToLeft4 {
  from {
    left: -300%;
  }
  to {
    left: -400%;
  }
}
@-webkit-keyframes moveToLeft5 {
  from {
    left: -400%;
  }
  to {
    left: -500%;
  }
}
@keyframes moveToLeft5 {
  from {
    left: -400%;
  }
  to {
    left: -500%;
  }
}
 


